:root {
  --background: #f5f3ee;
  --foreground: #333;
  --accent: #4a6fa5;
  --muted: #999;
  --divider: rgba(0, 0, 0, 0.08);
}
* {
  -webkit-font-smoothing: antialiased;
  -webkit-backface-visibility: hidden;
  box-sizing: border-box;
}
body {
  background-color: #f5f3ee;
  color: #333;
  font-family: var(--font-sans), system-ui, sans-serif;
  width: 100vw;
  overflow-x: hidden;
  word-spacing: 1px;
}
::selection {
  background-color: #333;
  color: #f5f3ee;
}
.accent-text {
  color: #4a6fa5;
}
.divider {
  height: 1px;
  background: rgba(0, 0, 0, 0.08);
}
@keyframes fadeSwap {
  0% {
    opacity: 0;
    transform: translateY(6px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes dotPing {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  75%, 100% {
    transform: scale(2.5);
    opacity: 0;
  }
}
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-track {
  background: #f5f3ee;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}
html {
  scroll-behavior: smooth;
}
.rotating-shape-container {
  --scroll-t: 0;
  position: fixed;
  z-index: 1;
  pointer-events: auto;
  cursor: pointer;
  transition: top 0.1s ease-out, left 0.1s ease-out, transform 0.1s ease-out;
}
.rotating-shape-left {
  top: calc(50vh - 32vh * var(--scroll-t));
  left: calc(50vw - 35vw * var(--scroll-t));
  transform: translate(-50%, -50%) scale(calc(1 - 0.6 * var(--scroll-t)));
}
.rotating-shape-right {
  top: calc(50vh - 32vh * var(--scroll-t));
  left: calc(50vw + 35vw * var(--scroll-t));
  transform: translate(-50%, -50%) scale(calc(1 - 0.6 * var(--scroll-t)));
}
a, button, span, div, p, h1, h2, h3, h4, h5, h6, nav, section, footer {
  transition: .3s cubic-bezier(.4, .4, 0, 1);
}
